import React, { useState } from "react";
import HTMLReactParser from "html-react-parser";
import { useParams } from "react-router-dom";
import millify from "millify";
import { Col, Row, Typography, Select } from "antd";
import {
  MoneyCollectOutlined,
  DollarCircleOutlined,
  FundOutlined,
  ExclamationCircleOutlined,
  StopOutlined,
  TrophyOutlined,
  CheckOutlined,
  NumberOutlined,
  ThunderboltOutlined,
} from "@ant-design/icons";

import LineChart from "./LineChart";
import {
  useGetCryptoDetailsQuery,
  useGetCryptoHistoryQuery,
} from "../services/CryptoApi";

const { Title, Text } = Typography;
const { Option } = Select;

const CryptoDetails = () => {
  const [timePeriod, setTimePeriod] = useState("7d");
  const { coinId } = useParams();
  const { data, isFetching } = useGetCryptoDetailsQuery(coinId);
  const { data: coinHistory } = useGetCryptoHistoryQuery({
    coinId,
    timePeriod,
  });

  const cryptoDetails = data?.data?.coin;
  // console.log(data);

  const time = ["3h", "24h", "7d", "30d", "1y", "3m", "3y", "5y"];

  const stats = [
    {
      title: "Price to USD",
      value: `$ ${cryptoDetails?.price && millify(cryptoDetails?.price)}`,
      icon: <DollarCircleOutlined />,
    },
    { title: "Rank", value: cryptoDetails?.rank, icon: <NumberOutlined /> },
    {
      title: "24h Volume",
      value: `$ ${
        cryptoDetails?.["24hVolume"] && millify(cryptoDetails?.["24hVolume"])
      }`,
      icon: <ThunderboltOutlined />,
    },
    {
      title: "Market Cap",
      value: `$ ${
        cryptoDetails?.marketCap && millify(cryptoDetails?.marketCap)
      }`,
      icon: <DollarCircleOutlined />,
    },
    {
      title: "All-time-high(daily avg.)",
      value: `$ ${
        cryptoDetails?.allTimeHigh?.price &&
        millify(cryptoDetails?.allTimeHigh?.price)
      }`,
      icon: <TrophyOutlined />,
    },
  ];

  const genericStats = [
    {
      title: "Number Of Markets",
      value: cryptoDetails?.numberOfMarkets,
      icon: <FundOutlined />,
    },
    {
      title: "Number Of Exchanges",
      value: cryptoDetails?.numberOfExchanges,
      icon: <MoneyCollectOutlined />,
    },
    {
      title: "Aprroved Supply",
      value: cryptoDetails?.supply?.confirmed ? (
        <CheckOutlined />
      ) : (
        <StopOutlined />
      ),
      icon: <ExclamationCircleOutlined />,
    },
    {
      title: "Total Supply",
      value: `$ ${
        cryptoDetails?.supply?.total && millify(cryptoDetails?.supply?.total)
      }`,
      icon: <ExclamationCircleOutlined />,
    },
    {
      title: "Circulating Supply",
      value: `$ ${
        cryptoDetails?.supply?.circulating &&
        millify(cryptoDetails?.supply?.circulating)
      }`,
      icon: <ExclamationCircleOutlined />,
    },
  ];
  if (isFetching) return "Loading... Please Wait";
  else
    return (
      <>
        <Col className='coin-detail-container'>
          <Col className='coin-heading-container'>
            <Title level={2} className='coin-name'>
              {cryptoDetails.name} ({cryptoDetails.symbol})
            </Title>
            <p>
              {cryptoDetails.name} live Price in US Dollars View value
              Satistics,market cap, and Supply
            </p>
          </Col>
          <Select
            defaultValue={"7d"}
            className='select-timeperiod'
            placeholder='Select Time Period'
            onChange={(value) => setTimePeriod(value)}
          >
            {time.map((date) => (
              <Option key={date}>{date}</Option>
            ))}
          </Select>

          <LineChart
            coinHistory={coinHistory}
            currentPrice={millify(cryptoDetails.price)}
            coinName={cryptoDetails.name}
          />

          <div className='stats-section mt-24 w-4/6 my-0 mx-auto flex justify-around'>
            {/* Specific Stats */}
            <div className='stat-section flex flex-col text-center'>
              <p className='text-3xl font-semibold'>
                {cryptoDetails.name} Value Statistics
              </p>
              <p>An Overview Showing the Stats of {cryptoDetails.name}</p>
              <div className='stat-details'>
                {stats.map(({ icon, title, value }) => (
                  <div className='coin-stats'>
                    <div className='coin-stats-name'>
                      <div>
                        <img src={icon} className='w-4 h-4' alt='' />
                      </div>
                      <div>{title}</div>
                    </div>
                    <p className='stats'>{value}</p>
                  </div>
                ))}
              </div>
            </div>
            {/* Generic Stats */}
            <div className='stat-section flex flex-col text-center'>
              <p className='text-3xl font-semibold'>Other Statistics</p>
              <p>An Overview Showing the Stats of All Cryptocurrencies</p>
              <div className='stat-details'>
                {genericStats.map(({ icon, title, value }) => (
                  <div className='coin-stats'>
                    <div className='coin-stats-name'>
                      <div>
                        <img src={icon} className='w-4 h-4' alt='' />
                      </div>
                      <div>{title}</div>
                    </div>
                    <p className='stats'>{value}</p>
                  </div>
                ))}
              </div>
            </div>
          </div>

          <div className='crypto-detail-sec flex justify-around w-5/6 my-0 mx-auto'>
            <p className='text-2xl w-4/6 p-10'>
              What is {cryptoDetails.name}
              {HTMLReactParser(cryptoDetails.description)}
            </p>
            <div className='w-2/6 p-10'>
              <p className='text-3xl'>{cryptoDetails.name} Links</p>
              {cryptoDetails.links.map((link) => (
                <div className='flex justify-between' key={link.name}>
                  <p className='text-xl'>{link.type}</p>
                  <a href={link.url} target='_blank' rel='noreferrer'>
                    {link.name}
                  </a>
                </div>
              ))}
            </div>
          </div>
        </Col>
      </>
    );
};

export default CryptoDetails;
